.list {
    display: flex;
    justify-content: flex-start;
    gap: 30px;
    flex-wrap: wrap;

    .shadow {
        box-shadow: 0px 6px 5px rgba(0, 0, 0, 0.24), 0px 9px 18px rgba(0, 0, 0, 0.18);
    }

    .block__img {
        position: relative;
        width: 225px;
        height: 225px;

        img {
            width: 100%;
            height: 225px;
        }

        .love {
            cursor: pointer;
            position: absolute;
            right: 8px;
            bottom: 8px;
            width: 40px;
            height: 40px;
        }

        .love__border {
            cursor: pointer;
            position: absolute;
            right: 8px;
            bottom: 8px;
            width: 40px;
            height: 40px;
        }
    }
}
